:root {
    font-size: 16px;
    font-family: 'Spartan';
    --text-primary: #b6b6b6;
    --text-secondary: #ececec;
    --bg-primary: #23232e;
    --bg-secondary: #141418;
    
}
.home {
    height: 100vh;
    position: absolute;
    width: 100%;
    
}
.home {
    background-image: linear-gradient(to top, #e6e9f0 0%, #eef1f5 100%);
}
.home-content {
    height: 100%;
    display: flex;
    justify-content: flex-start;
    vertical-align: center;
    img{
        margin-top: 15% ;
        height:35%;
    }
}

.home-presentation {
    margin: auto 2rem auto 0;
    padding:0 0 0 6rem;
    width: 45%;
    text-align: center;
    h4 {
        font-size: 3rem;
        margin-bottom: 0 0 0 0;
    }
}


body {
    color: black;
    background-color: white;
    margin:0;
    padding:0;
    background: src;

}
body::-webkit-scrollbar {
    width: 0.5rem;
}

body::-webkit-scrollbar-track {
    background: #1e1e24;
}

body::-webkit-scrollbar-thumb {
    background: #4bc413;
}

main {
    margin-left: 5rem;
    padding: 0;
}

.navbar {
    position: fixed;
    background-color: var(--bg-primary);
    transition: width 200ms ease;
    z-index: 9999;
}

.navbar-nav {
    list-style: none;
    padding: 0;
    margin:0;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%; 
}

.nav-item {
    width: 100%;
}

.nav-item:last-child {
   margin-top: auto; 
}

.nav-link {
    display:flex;
    align-items: center;
    height: 5rem;
    color: var(--text-primary);
    text-decoration: none;
    filter: grayscale(100%) opacity(0.7);
    transition: var(--transition-speed);
}

.nav-link:hover {
    filter: grayscale(0%) opacity(1);
    background: var(--bg-secondary);
    color: var(--text-secondary)
}

.link-text {
    display: none;
    margin-left: 1rem;
}

.nav-link svg {
    min-width: 2rem;
    margin: 0 1.5rem;
}

/* .navbar:hover{
    width: 16rem;
} */

.navbar:hover .link-text {
    display: block;
}

.fa-primary {
    color: #305F82
}

.fa-secondary {
    color: rgb(124, 152, 173)
}
.fa-primary,
.fa-secondary {
    transition: var(--transition-speed);
}

.logo {
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 1rem;
    text-align:center;
    color: var(--text-secondary);
    background: var(--bg-secondary);
    text-transform: uppercase;
    font-size: 1.5rem;
    letter-spacing: 0.3ch;
    width: 100%;
}

.logo svg {
    transform: rotate(0deg);
    transition: all 600ms;
}
.navbar:hover .logo svg {
    transform: rotate(-180deg);
}

@media only screen and (max-width: 600px) {
    .navbar {
        bottom: 0;
        width: 100vw;
        height: 5rem;
    }
    .section {
        margin: 0;
        padding: 0;
        flex-direction: column;
    }

    .logo {
        display: none;
    }
    .navbar:hover .link-text {
        display: none;
    }

    .navbar-nav {
        flex-direction: row;
    }

    .nav-link {
        justify-content: center;
    }

    main {
        margin: 0;
    }
}

@media only screen and (min-width: 600px){
    .navbar {
        top: 0;
        width: 5rem;
        height: 100vh;
    }
    

    .navbar:hover {
        width: 16rem;
    }

    .navbar:hover .link-text {
        display: inline;
        transition: opacity var(--transition-speed);
    }
}